.top-HomeMUN
{
    display: flex;
    justify-content: center;
    margin-top: 20vh;
}
.top-HomeMUN-img
{
    width: 50vw;
}
.registerMUNNowBtn
{
    display: flex;
    justify-content: center;
}
.registerMUNNowBtnClick
{
    /* width: 20vw; */
    border-radius: 16px;
    color: #000;
    font-family: Bangers;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%;
    /* 32px */
    letter-spacing: 1.6px;
    padding: 1vh 2.5vw;
    background-color: #fff;
    margin-top: 19vw;
}
.registerMUNNowBtnClick:hover
{
    background-color: #FDD52E;
}
.prizesMUNWorth {
    border-radius: 14px;
    background: rgba(8, 48, 114, 0.40);
    color: #FFF;
    text-align: center;
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 162.2%;
    /* 32.44px */
    letter-spacing: 1px;
    width: 50vw;
    margin: auto;
    margin-top: 30vh;
    padding: 1.5vh 2vw;
    padding-bottom: 2.5vh;
}


.prizesMUNWorth1 {
    width: 35vw;
    display: block;
    margin-bottom: 5vh;
}

.prizesMUNWorthDiv {
    display: flex;
    justify-content: center;
}

.CommitteeHead
{

    display: flex;
    justify-content: center;
}
.CommitteeHead img{
    width: 40vw;
}
#committeeHead1 img{
    margin-top:15vh;
    margin-bottom: -2vh;
}
#committeeHead2 img{
    margin-top:9vh;
    width:55vw;
}


