@import url("https://fonts.googleapis.com/css2?family=Bangers&family=Roboto:wght@700&display=swap");
.page-2-merch {
  width: 100vw !important;
  overflow-x: hidden !important;
}
.shirt-container {
  width: 100vw;
  display: flex;
  flex-direction: row;
  padding: 5vh 10vw 4vh 10vw;
  background-color: #fff;
}
.shirt-container1 {
  width: 35vw;
  display: flex;
  flex-direction: column;
}
.shirt-container2 {
  padding-left: 5vw;
  width: 35vw;
}
.big-box2 {
  display: flex;
  flex-direction: row;
  /* justify-content: space-between; */
  gap: 2vw;
}
.box-img1 {
  padding-top: 4vh;
  height: 10vw;
  width: 10vw;
}
.tshirt-head {
  color: #202020;
  font-family: Bangers;
  font-size: 2.5vw;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 48px */
  letter-spacing: 3.2px;
  text-transform: lowercase;
}
.box-img2 {
  height: 60vh;
  width: 25vw;
}
.tshirt-price {
  display: flex;
  flex-direction: row;
  padding-top: 2vh;
  align-items: center;
}
.price1 {
  color: rgba(42, 42, 42, 0.98);
  font-family: Bangers;
  font-size: 2vw;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 30px */
  letter-spacing: 0.4px;
  /* text-transform: lowercase; */
  padding-right: 1vw;
}
.price3 {
  color: #757575;
  font-family: Bangers;
  font-size: 2vw;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 30px */
  letter-spacing: 0.4px;
  /* text-transform: lowercase; */
  padding-right: 1vw;
}
.price1-offer {
  color: #f26120;
  font-family: Comic Sans MS;
  font-size: 1.4vh;
  font-style: normal;
  font-weight: 700;
  line-height: 150%; /* 24px */
  letter-spacing: 0.32px;
  /* text-transform: lowercase; */
}
.inclu {
  color: #00be6e;
  font-family: Comic Sans MS;
  font-size: 2vh;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 24px */
  letter-spacing: 0.32px;
  text-transform: lowercase;
}
.hr1 {
  height: 0.2vh;
  width: 50vw;
  margin: 2vh 0;
}
.color12 {
  color: #757575;
  font-family: Comic Sans MS;
  font-size: 2vh;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 30px */
  letter-spacing: 0.4px;
  text-transform: lowercase;
  display: flex;
  flex-direction: row;
}
.color122 {
  color: #2c2c2c;
}
.Quantity {
  display: flex;
  flex-direction: row;
  margin-top: 0.5rem;
  margin-top: 1rem;
}
.quantityFont {
  color: #2c2c2c;
  font-family: Comic Sans MS;
  font-size: 1.25rem;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 30px */
  letter-spacing: 0.4px;
  text-transform: lowercase;
  margin-right: 1rem;
  

}
.arithmatic{
  width: 2rem;
  height: 2rem;
  border: none;
  background: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.colorbox {
  display: flex;
  flex-direction: row;
  gap: 1vw;
  padding-top: 2vh;
}
.colorbox1 {
  border: 1px solid black;
  height: 4vh;
  width: 2.5vw;
  background-color: white;
}

.colorbox1A {
  border: 3px solid black;
  height: 4vh;
  width: 2.5vw;
  background-color: white;
}

.colorbox2 {
  border: 1px solid black;
  height: 4vh;
  width: 2.5vw;
  background-color: black;
}
.colorbox3 {
  border: 1px solid black;
  height: 4vh;
  width: 2.5vw;
  background-color: #009ddb;
}
.size {
  color: #2c2c2c;
  font-family: Comic Sans MS;
  font-size: 2vh;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 30px */
  letter-spacing: 0.4px;
  text-transform: lowercase;
  padding-top: 3vh;
}
.select-size {
  display: flex;
  flex-direction: row;
  gap: 1.5vw;
  padding-top: 3vh;
}
.size1 {
  border: 2px solid black;
  height: 4vh;
  width: 2.5vw;
  /* padding: auto; */
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5vh;
  border-radius: 100%;
  padding: 1.5vw;
  cursor: pointer;
}
.size2 {
  border: 2px solid black;
  height: 4vh;
  width: 2.5vw;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  padding: 1.5vw;
  border-color: #F26120;
  color: #F26120;
  cursor: pointer;
}
.bag1 {
  /* border:2px solid black; */
  height: 5vh;
  width: 15vw;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 3vh;
  border-radius: 4px;
  background: #f26120;
  border: none;
  padding-right: 1vw;

  color: #fff;
  font-family: Bangers;
  font-size: 2.5vh;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 36px */
  letter-spacing: 2.4px;
  text-transform: lowercase;
}
.lock5 {
  height: 2vh;
  width: 2vw;
}
.con1 {
  padding-top: 0.4vh;
  padding-left: 0.5vw;
}

@media only screen and (max-width: 768px) {
  .shirt-container {
    flex-direction: column;
    align-items: center;
    /* padding-left:4vw ; */
  }
  .shirt-container2 {
    padding-top: 4vh;
    width: 80vw;
    padding-left: 0vw;
  }
  .shirt-container1 {
    width: 80vw;
  }
  .box-img1 {
    height: 30vw;
    width: 23vw;
  }
  .box-img2 {
   width: 80vw;
  }
  .tshirt-head {
    font-size: 7.5vw;
  }
  .price1 {
    font-size: 5vw;
    padding-right: 3vw;
  }
  .price3 {
    font-size: 5vw;
    padding-right: 3vw;
  }
  .price1-offer {
    font-size: 2.4vh;
  }
  .inclu {
    font-size: 3vh;
    padding-top: 2vh;
  }
  .colorbox1 {
    width: 6.5vw;
  }
  .colorbox2 {
    width: 6.5vw;
  }
  .colorbox3 {
    width: 6.5vw;
  }
  .colorbox {
    gap: 3vw;
  }
  .color12 {
    font-size: 3vh;
  }
  .size {
    font-size: 3vh;
    gap: 5px;
  }
  .bag1 {
    height: 5vh;
    width: 34vw;
  }
  .size1 {
    width: 7.5vw;
    height: 5vh;
  }
  .size2 {
    width: 7.5vw;
    height: 5vh;
  }
  .size3 {
    width: 7.5vw;
    height: 5vh;
  }
  .size4 {
    width: 7.5vw;
    height: 5vh;
  }
  .lock5 {
    width: 3vw;
  }
}
